* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --cor0: #53DBC9;
    --cor1: #6EA29B;
    --cor2: #368E82;
    --cor3: #07393C;
    --cor4: #2D5D7B;
    --cor5: #0f547e;
    --cor6: #062234;
    --cor7: #B3F2DD;
    --cor8: #000500;
    --cor9: #65AFFF;
    --cor10: #75dbcd;
}

.header {
    font-family: 'Futura PT', sans-serif;                                                
    background-image: linear-gradient(to bottom, #000500, #0f547e);                                                
    background-image: linear-gradient(to bottom, var(--cor8), var(--cor5));
    color: #edf9ef;
    padding: .5em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.nav-link {
    color: #d2e6fc;
}

.nav-link:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

.nav-link:hover {
    color: #65AFFF;
    color: var(--cor9);
}

.header a {
    text-decoration: none;
    font-size: 1.3em;
    color: #EDF9EF;
    font-weight: 400;
}

.btn-dark {
    background-color: #65AFFF;
    background-color: var(--cor9);
    border-style: none;
}

.btn-dark:hover {
    background-color: #4ca2fe;
}

.btn-dark:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}

@media screen and (max-width: 960px){
    .header {
        flex-direction: row;
        align-items: center;
    }   
}
